// es6promise
var promise = require("utils/es6-promise.js");
import myCache from '@/utils/myCache.js'
/**
 * @author azhou
 * @params fn 
 * @return promise
 * @ es6promise
 * @  http://es6.ruanyifeng.com/?spm=a2c4e.11153940.blogcont325262.8.573c367b8BG2hG#docs/promise#%E5%BA%94%E7%94%A8
 */
function myPromise(fn) {
	return function(obj = {}) {
		return new Promise((resolve, reject) => {
			obj.success = function(res) {
				console.log(res)
				//0
				//catch
				if (res.code == 404) {
					console.log("")
					return reject();
				}
				if (res.data.data == null) {
					res.data.data = {}
				}
				if (res.data.code > 10000) {
					return reject();
				}
				if (res.data.code != 200) {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
					return reject();
				}
				return resolve(res.data);
			};
			obj.fail = function(err) {
				//
				if (err.msg == null || err.msg == '' || err.msg == 'SUCCES') {
					err.msg = '~'
				}
				uni.redirectTo({
					url: '/packageA/pages/notFound/notFound'
				})
				console.log("requestUtil=====err======");
				console.log(err);
				reject(err);
			};
			fn(obj);
		})
	}
}
/**
 * @author azhou
 * @params fn 
 * @return promise
 * @ es6promise
 * @  http://es6.ruanyifeng.com/?spm=a2c4e.11153940.blogcont325262.8.573c367b8BG2hG#docs/promise#%E5%BA%94%E7%94%A8
 * 404
 */
function myPromiseNormal(fn) {
	return function(obj = {}) {
		return new Promise((resolve, reject) => {
			obj.success = function(res) {
				//0
				//catch
				console.log('', res.data)
				if (res.data.data == null) {
					res.data.data = {}
				}
				return resolve(res.data);
			};
			obj.fail = function(err) {
				//
				if (err.msg == null || err.msg == '' || err.msg == 'SUCCES') {
					err.msg = '~'
				}
				console.log("requestUtil=====err======");
				console.log(err);
				reject(err);
			};
			fn(obj);
		})
	}
}

/**
 * promise
 */
Promise.prototype.finally = function(callback) {
	let p = this.constructor;
	return this.then(
		value => p.resolve(callback()).then(() => value),
		reason => p.resolve(callback()).then(() => {
			throw reason
		})
	)
}

/**
 * requestget
 * @author azhou
 * @params url 
 * @params data 
 * @return promise
 */
function myGetRequest(url, data) {
	var myGetRequest = myPromise(uni.request);
	return myGetRequest({
		url: url,
		data: data,
		method: 'GET',
		header: {
			'Content-Type': 'application/json',
			"token": myCache.cache("userToken")
		}
	});
}

